<template>
<div>
    <Toppanel/>
    
    
    <div class="add-new-btn" @click="newClick">+</div>
    <div class="add-new-tab" v-if="newClicked">
        <button class="tab-item" id="new-friend" v-if="fg" @click="addFriend">+ Add Friend</button>
        <!-- <button class="tab-item" id="new-group" v-if="fg" @click="addGroup">+ Add Group</button> 
        <button class="tab-item" id="new-group" v-if="fg" @click="addGroup">> Join Group</button>  -->
        <input type="text" class="nametag-inp" v-if="addFClicked" placeholder="Friend's nametag" v-model="friend_request">
        <input type="text" class="nametag-inp" v-if="addGClicked">
        <div class="okno-outer" v-if="okno">
            <div class="okno" @click="newRequest"><div></div></div>
            <div class="okno" @click="newClick">&times;</div>
        </div>  
    </div>
    <div class="lobby-main">
        <div class="lobby-item" v-bind:key="m.fr_id" v-for="m in user_friends" @click="navToChat(m.fr_id)">
            <div class="img-container"></div>
            <div class="nametag">{{m.nametag}}</div>
        </div>
    </div>
    
</div>    
</template>

<script>
import Toppanel from './Toppanel'
import firebase from 'firebase'
export default {
    name:'Lobby',
    components:{
        Toppanel
    },
    data(){
        return{
            user_tag:'',
            user_email:'',
            user_friends:[],
            convoThreads:[],
            friend_request:'',
            newClicked:false,
            fg:true,
            addFClicked:false,
            addGClicked:false,
            okno:false,
            current_join_data:[],
            current_join_route:''    
        }
    },
     created(){
    let user = firebase.auth().currentUser;
    let fs = firebase.firestore();
    fs.collection("users").doc(user.uid).onSnapshot((doc)=>{
       if(doc.exists){
         this.user_tag=doc.data().r_nametag;
         this.user_friends=doc.data().r_friends;
         this.convoThreads=doc.data().r_convoThreads;
       }else{
         console.log('failed to parse')
       }
     })
    this.user_email=user.email;
    
  },
    methods:{
        async navToChat(fr_id){
            let fs_join = firebase.firestore();
            var this_user_convoThread= this.convoThreads;
            await fs_join.collection("users").doc(fr_id).onSnapshot((doc)=>{
                if(doc.exists){
                    this.current_join_data=doc.data().r_convoThreads
                    outer_loop:
                for(var i=0;i<this_user_convoThread.length;i++){
                for(var j=0;j<this.current_join_data.length;j++){
                    if(this_user_convoThread[i] === this.current_join_data[j]){
                        this.current_join_route=this_user_convoThread[i];
                        
                        var con_path = '/rhymeappchat/'+this.current_join_route;
                        this.$router.push({path: con_path });
                        break outer_loop;
                    }
                }
            }
                }else{
                     console.log('failed to parse')
                    }
                 });
            
            
            
        },
        newClick(){
            this.newClicked = !this.newClicked;
            
            this.fg = true;
            this.okno=false;
            
            this.addFClicked=false;
            this.addGClicked=false;
            this.friend_request='';
            
        },
        addFriend(){
            this.fg = !this.fg;
            this.okno=!this.okno;
            this.addFClicked=!this.addFClicked;
        },
        addGroup(){
            this.fg = !this.fg;
            this.okno=!this.okno;
            this.addGClicked=!this.addGClicked;
        },
        async newRequest(){
            const fs = firebase.firestore();
                let user = firebase.auth().currentUser;
                var addfr = this.friend_request;
                var userfr=[]
                this.user_friends.map(item=>{
                    userfr.push(item.nametag);
                })
                 
            if(! userfr.includes(addfr)){
                
                
            var newFrnd = {id:user.uid,tag:this.user_tag};
            if(this.addFClicked && this.friend_request != this.user_tag && this.friend_request != ''){
                
                await fs.collection("users").where("r_nametag", "==", this.friend_request)
                    .get()
                    .then(function(querySnapshot) {
                        querySnapshot.forEach(function(doc) {
            // doc.data() is never undefined for query doc snapshots
                            
                            var newFs = fs.collection("users").doc(doc.id)
                                newFs.update({
                                    r_pendingReqs: firebase.firestore.FieldValue.arrayUnion(newFrnd)
                                    }).catch((error)=>{
                                        console.log("Error getting documents: ", error);
                                    });    
                        });
                                }).then(()=>{
                                    this.friend_request = ''
                                })
                        .catch(function(error) {
                        console.log("Error getting documents: ", error);
                        });
            }
            }
        }


    }
}
</script>

<style scoped>
.lobby-main{
    /* background-color: burlywood; */
    margin-top: 4rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lobby-item{
    width: 90%;
    height: 3.5rem;
    background-color: rgb(26, 19, 32);
    margin:0.4rem;
    border-radius: 1rem 1rem 0 1rem;
    border:1px solid rgb(151, 111, 151);
    box-shadow:0.3rem 0.3rem  rgb(56, 42, 94);
    padding:0.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    
}
.lobby-item:hover{
    background-color: rgb(29, 21, 37);
}
.img-container{
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    background-color: rgb(64, 49, 80);
    
    background-size: cover;
}
.nametag{
    margin-left:1rem ;
    /* background-color: rgb(64, 49, 80); */
    border-radius: 0.1rem;
    color: White;
    font-family: 'Poppins', sans-serif;
}
.add-new-btn{
    width:3rem;
    height: 3rem;
    border-radius:50%;
    background-color: rgb(34,34,69);
    text-align: center;
    line-height: 3rem;
    font-size: 2rem;
    color: rgb(106, 79, 146);
    position: fixed;
    bottom: 0;
    right:0;
    margin:2rem;
    box-shadow: 2px 3px 3px rgb(23, 23, 46);
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    cursor: pointer;
}
.add-new-btn:hover{
    background-color: rgb(39, 39, 73);  
}
.add-new-tab{
    
    width: 14rem;
    
    padding: 0.5rem;
    background-color: #181830;
    position: absolute;
    bottom: 0;
    right:0;
    margin-right: 2rem;
    margin-bottom: 5.5rem;
    border-radius: 0.5rem;
    box-shadow: 2px 2px 2px rgb(23, 23, 46);
    color: rgb(106, 79, 146);
    font-family:'Poppins', sans-serif;
    display: flex;
    flex-direction:column;
    align-items: center;
}
.tab-item{
    margin: 0.2rem;
    width: 12rem;
    height: 2rem;
    border: 0;
    border:1px solid white;
    background-color: #181830;
    color: white;
    outline:0;
    font-family: 'Poppins',sans-serif;
}
.tab-item:hover{
   background-color: #1f1f3a; 
}
.okno-outer{
    display: flex;
    flex-direction:row ;

}
.okno{
    margin: 0.5rem;
    width:2rem;
    height:2rem;
    border:1px solid white;
    border-radius: 50%;
    text-align: center;
    color: rgb(241, 79, 79);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    cursor: pointer;
}
.okno:hover{
        background-color: #1f1f3a; 
}
.okno>div{
    width:0.5rem;
    height:0.5rem;
    border:2px solid rgb(51, 245, 180);
    border-radius: 50%;
     
}
.nametag-inp{
    margin-top: 0.8rem;
    height: 1.7rem;
    width: 11rem;
    padding-left:0.5rem ;
    padding-right:0.5rem ;
    background-color: #232344;
    border: 1px solid white;
    border-radius: 0.2rem;
    color: white;
    outline: 0;
    font-family: 'Poppins',sans-serif;
}

</style>